import { string } from "prop-types";

type Actions = 
    | {type: 'openCollections', boolean: boolean} 

interface initstate {
    collections: Array<{}>;
    current: string;
    open: boolean;
}

const collectionsReducer = (state: initstate , action:Actions) => {
    switch(action.type) {
        case "openCollections":
            return {...state, open: action.boolean}
        default:
            return state;
    }
}

export default collectionsReducer